body {
  margin: 0;
  height: 100%;
  font-family: 'PT Sans', sans-serif;
}

.page-layout {
  height: 100vh;
}

.first-layout {
  background-color: #3c8d93;
}

.second-layout {
  height: 60vh;
  background-color: #085e72;
}

.third-layout {
  height: 80vh;
  background-color: #021115;
}

.first-content {
  height: 100%;
  display: flex;
  justify-content: left;
  align-items: center;
  margin-left: 100px;
  line-height: 60px;
}
.second-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.third-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.data,
.fullname {
  color: white;
}
.data {
  font-size: 20px;
}

.fullname {
  font-size: 70px;
  font-weight: 700;
}

.social ul {
  padding: 0;
  margin: 0;
}

.social ul li {
  display: inline;
  margin-right: 10px;
}

.social ul li a img {
  height: 25px;
}

.header {
  font-size: 60px;
  font-weight: bold;
  color: white;
}

.ex {
  margin-right: 45px;
}
.ex-container {
  display: flex;
  flex-direction: row;
  color: white;
  flex-wrap: wrap;
}

.project-container {
  display: flex;
  flex-wrap: wrap;
}
.project-container > div > div {
  flex-grow: 1;
  width: 33%;
  height: 100px;
}

.project-group {
  display: flex;
  justify-content: center;
  align-items: center;
}
.project {
  width: 350px;
  height: 200px;
  /* margin-right: 45px; */
  background-size: cover;
  background-color: white;
  background-repeat: no-repeat;
  transition: all 0.5s;
}

.wip {
  background-image: url('./img/logo-wip.png');
}

.signup {
  background-image: url('./img/signup.png');
}

.moneydi {
  background-image: url('./img/money.png');
}
.ywc {
  background-image: url('./img/ywc.png');
}

.wip:hover,
.signup:hover,
.moneydi:hover,
.ywc:hover {
  transform: scale(1.05);
}

.topic-ex {
  font-weight: bold;
  font-size: 17px;
}

@media only screen and (max-width: 600px) {
  .first-content {
    margin-left: 50px;
  }
  .fullname {
    font-size: 50px;
  }

  .second-content {
    align-items: unset;
    justify-content: center;
    margin-left: 50px;
  }
  .second-layout,
  .third-layout {
    height: 100vh;
  }

  .header {
    font-size: 50px;
  }
  .project {
    margin: 10px 10px;
    width: 325px;
    height: 185px;
  }
  .project-container {
    justify-content: center;
  }
}
